import React, { useState, useEffect } from 'react'
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { ExpandLess } from '@material-ui/icons';
import {
    Card, CardActionArea, CardMedia, Grid, Typography, ListItem, List,
    Accordion, AccordionSummary, AccordionDetails, Dialog, DialogContent,
    DialogContentText, InputBase, Button
} from '@material-ui/core';



const useStyles = makeStyles(() =>
    createStyles({
        root: {
            flexGrow: 1,
            color: 'white',
        },
        centerElement: {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
        },
        listItem: {
            overflow: 'scroll',
            maxHeight: '10rem',
            overflowX: 'hidden',
            backgroundColor: 'lightgray',
            padding: '0', margin: '0'
        },
        accordion: {
            width: '100%',
        },
        dialogHead: {
            width: '100%',
            textAlign: 'center',
            fontSize: '24px',
            fontFamily: 'Roboto'
        },
        dialog: {
            overflow: 'hidden'
        },
        card: {
            position: 'relative',
        },
        overlayFont: {
            position: "absolute",
            top: "40%",
            width: "100%",
            textAlign: "center",
            color: "white",
            fontFamily: "Roboto",
        },
        flexContainer: {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            padding: 0,
            color: 'black',
            width: '100%',

        }
    })
)



function DisplayContent(props) {
    const [open, setOpen] = useState(false);
    const [item, setItem] = useState({});
    const [loading, setLoading] = useState(false);
    const [expanded, setExpanded] = useState(false);
    const [hideNoImage, setHideNoImage] = useState(true)



    const handleOpen = (props) => {
        setOpen(!open);
        setItem(props)
    }
    const handleClose = () => {
        setOpen(!open);
        setItem({})
    }
    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false)
    }
    const data = props.controls.data
    const classes = useStyles();
    const noImage = 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available'
    const placeholderImage = "http://i.annihil.us/u/prod/marvel/i/mg/f/60/4c002e0305708"


    useEffect(async () => {
        { props.controls.type === undefined ? await setLoading(true) : await setLoading(false) }
    })

    const changeContent = (item) => {
        props.changeContent(item);
    }

    const displayAccordion = (props) => {
        const type = props.type
        return (
            type.map((data, index) => (
                data.data !== undefined && data.data.available > 0 &&
                <Accordion expanded={expanded === index} onChange={handleChange(index)} style={{ margin: '0', padding: '0' }}>
                    <AccordionSummary
                        expandIcon={<ExpandLess />}
                        style={{ backgroundColor: 'gray' }}
                    >

                        <Typography className={classes.accordion} key={index} variant="h6">
                            <span>{data.name}</span>
                            <span style={{ float: 'right' }}>({data.data.available})</span>

                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails className={classes.listItem}>
                        <Typography>
                            {data.data !== undefined && data.data.items.map((listItem, index) => (
                                <AccordionDetails key={index}>
                                    <List>
                                        <ListItem button onClick={() => changeContent({item: listItem, type: data.name})}>{listItem.name}</ListItem>
                                    </List>
                                </AccordionDetails>
                            ))}
                        </Typography>
                    </AccordionDetails>
                </Accordion>

            ))
        )
    }

    const options = () => {
        return (
            <Grid container style={{ paddingBottom: '2em' }}>

                <Button variant="contained" onClick={props.nextPage}>
                    Next Page
            </Button>
                {props.controls.offset !== 0 &&
                    <Button variant="contained" onClick={props.prevPage}>
                        Previous Page
            </Button>
                }
     
                <InputBase
                    placeholder="Searchâ€¦"
                    style={{ backgroundColor: 'white' }}
                    classes={{
                    }}
                />
                           <Button onClick={() => setHideNoImage(!hideNoImage)} variant="contained">
                    Hide {props.controls.type} with no Images
            </Button>
            </Grid>
        )
    }

    return (
        <div className={classes.root}>
            {options()}
            <Typography item md={12} style={{ color: 'white', fontSize: '20px', paddingBottom: '1em' }}>
                    Showing ({props.controls.offset + 1} - {props.controls.offset + 18}) of {props.controls.total} Results Found
            </Typography>
            <Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm">
                <Accordion defaultExpanded style={{ margin: '0', padding: '0' }}>
                    <AccordionSummary>
                        <Typography className={classes.dialogHead}>{item.name || item.title}</Typography>
                    </AccordionSummary>
                    <AccordionDetails style={{ margin: '0', padding: '0' }} >
                        <DialogContent className={classes.dialog} style={{ margin: '0', padding: '0' }}>

                            <Grid container spacing={2} className={classes.centerElement}>
                                <Grid item md={10}>
                                    <DialogContentText>
                                        {item.thumbnail !== undefined &&
                                            <Card className={classes.card}>
                                                <CardMedia component="img"
                                                    className={classes.dialogImg}
                                                    image={item.thumbnail.path + '/standard_incredible.' + item.thumbnail.extension}
                                                    title={item.name}
                                                />
                                                {item.thumbnail.path === noImage &&
                                                    <Typography variant="h3" className={classes.overlayFont}>
                                                        {item.name || item.title}
                                                    </Typography>
                                                }
                                            </Card>

                                        }
                                    </DialogContentText>
                                </Grid>
                                <Grid item md={12}>
                                    <DialogContentText id="alert-dialog-slide-description">
                                        <Typography style={{ margin: '1em' }}>{item.description}</Typography>
                                    </DialogContentText>
                                </Grid>
                            </Grid>

                        </DialogContent>
                    </AccordionDetails>
                </Accordion>
                {props.controls.type === 'characters' &&
                    displayAccordion
                        ({
                            type:
                                [
                                    {
                                        name: 'Comics', data: item.comics
                                    },
                                    {
                                        name: 'Stories', data: item.stories
                                    },
                                    {
                                        name: 'Events',data: item.events
                                    }
                                ]
                        })
                }
            </Dialog>

            <Grid container spacing={2}>
                {(data !== undefined && loading === false) ? data.map((item, index) => {
                    return (
                        <>
                            {(item.thumbnail.path !== noImage && item.thumbnail.path !== placeholderImage || hideNoImage === false) &&
                                <Grid key={index} lg={2} md={3} sm={4} xs={6} item className={classes.items}>
                                    <Card>
                                        <CardActionArea>
                                            <CardMedia
                                                component="img"
                                                image={item.thumbnail.path + '/portrait_uncanny.' + item.thumbnail.extension}
                                                title={item.name}
                                                onClick={() => handleOpen(item)}
                                            />
                                            {item.thumbnail.path === noImage &&
                                                <Typography variant="h5"
                                                            component="div"
                                                            className={classes.overlayFont}
                                                >
                                                    {item.name || item.title || item.fullName}
                                                </Typography>
                                            }
                                        </CardActionArea>
                                    </Card>
                                </Grid>
                            }
                        </>
                    )
                }) :
                //Not my code, just a placeholder 
                    <div className="loaderBody"> 
                        <div className="loader">
                            <span>L</span>
                            <span>O</span>
                            <span>A</span>
                            <span>D</span>
                            <span>I</span>
                            <span>N</span>
                            <span>G</span>
                        </div>
                    </div>
                }

            </Grid>
        </div>
    )
}

export default DisplayContent;

