import './App.css';
import React, {useState, useEffect, useRef} from 'react'
import SideBar from './Components/SideNavBar'
import SearchApi from './Components/SearchApi'
import DisplayContent from './Components/DisplayContent'
import {Grid} from '@material-ui/core';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import Search from './Components/search';


const useStyles = makeStyles(() =>
    createStyles({
        root: {
            flexGrow: 1,
            padding: '0em 10% 0em 10%',
            minHeight: '100vh',
            backgroundColor: 'black',
        },
        items: {
            border: '1px solid green'
        }
    })
)


function App() {
  const [controls, setControls] = useState({type: 'characters', data: []})
  const classes = useStyles();

  useEffect(async () => {
    controls.data.length === 0 && getMediaType({type: controls.type, offset: 0})
  }, [])

  const getMediaType = async (props) => {
        setControls({...controls, type: undefined})
        const requestedMedia = await Search({type: props.type, offset: props.offset, defaultSearch: true});
        await setControls({...controls, type: props.type,
                                  total: requestedMedia.total,
                                  offset: requestedMedia.offset,
                                  count: requestedMedia.count,
                                  limit: requestedMedia.limit,
                                  data: requestedMedia.results
                                });
  }

  const nextPage = () => {
    getMediaType({type: controls.type, offset: controls.offset+18})
  }
  const prevPage = () => {
    getMediaType({type: controls.type, offset: controls.offset-18})
  }

  const changeContent = async (props) => {
    const requestContent = await Search({defaultSearch: false, item: props.item, type: props.type});
  }

  return (
    <>
    <SearchApi/>
      <div className={classes.root}>
          <Grid container style={{display: 'flex', justifyContent: 'center'}} spacing={2}>
          <Grid style={{padding: '0',}} item xs={10}><SideBar controls={controls} value={getMediaType} /></Grid>
          <Grid zeroMinWidth item xs={12}><DisplayContent nextPage={nextPage} prevPage={prevPage} changeContent={changeContent} controls={controls} /></Grid>
        </Grid>
      </div>
      </>
  );
}

export default App;
