import React from 'react'
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { ListItem, Grid, Typography, ListItemText, InputBase, List } from '@material-ui/core';
import { SidebarData } from './SideBarData';


const useStyles = makeStyles(() =>
    createStyles({
        root: {
            
        },
        activeType: {
            backgroundColor: 'lightgray',
        },
        types: {
            color: 'white',
        },
        header: {
            textAlign: 'center',
            color: 'black',
            fontSize: '24px',
            fontFamily: 'Roboto',
        },

        listItems: {
            padding: '.15em',
            textAlign: 'center',
        },
        nav: {
            padding: '0',
            margin: '0',
            borderRadius: '1em',
            border: '1px solid black'
        },
        flexContainer: {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            padding: 0,
            color: 'black',
            width: '100%'

        }
    })
)

function SideBar(props) {
    const classes = useStyles();
    const getMediaType = props.value

    return (
        <div className={classes.root}>
            <Grid container className={classes.flexContainer}>
            {SidebarData.map((item, index) => (
                <Grid key={index} item md={2} sm={3} xs={4}>
                <ListItem className={classes.listItems} key={index} button onClick={() => getMediaType({type: item.type, offset: 0})}>
                    <ListItemText
                        primary={<Typography variant="h6">{item.primary}</Typography>}
                        className={props.controls.type === item.type ? classes.activeType : classes.types}
                    />
                </ListItem>
                </Grid>
            ))}
            <ListItem>
            </ListItem>
                
            </Grid>

                
            
        </div>
    )
}

export default SideBar;